const initWho = () => {
    
        // Элементы для секции who()
        const whoModalBtns = document.querySelectorAll('.who__btn[data-op]');
        const whoModals = document.querySelectorAll('.modal');
        const whoOverlay = document.querySelector('.modal-over');
        const body = document.body;
        // Открытие модалки
        whoModalBtns.forEach(btn => {
          btn.addEventListener('click', function() {
            const modalId = this.getAttribute('data-op');
            const modal = document.getElementById(modalId);
            
            if (modal) {
              whoOverlay.style.display = 'block';
              modal.style.display = 'block';
              body.classList.add('modal-open');
              // Логи для отладки
              console.log('Opening modal:', modalId);
              console.log('Modal element:', modal);
            } else {
              console.error('Modal not found:', modalId);
            }
          });
        });
        
        // Функция закрытия
        function closeWhoModal() {
          whoOverlay.style.display = 'none';
          whoModals.forEach(modal => {
            modal.style.display = 'none';
            body.classList.add('modal-open');
          });
        }
        
        // Закрытие по клику на оверлей (вне модалки)
        if(whoOverlay) {
          whoOverlay.addEventListener('click', function(e) {
            if (e.target === whoOverlay) {
              closeWhoModal();
            }
          });
        }
        
        
        // Закрытие по крестику
        whoModals.forEach(modal => {
          modal.querySelector('.modal__close').addEventListener('click', closeWhoModal);
        });
        
        // Закрытие по ESC
        document.addEventListener('keydown', function(e) {
          if (e.key === 'Escape') {
            closeWhoModal();
          }
        });
    

}

export {initWho};